<template>
  <div class="impact9Container">
    <h2>Impact 9</h2>
    <!-- ADDED: Points selection before coin flip -->
    <div v-if="!gameStarted && !pointsSelected" class="points-selection">
      <h3>Sélectionnez le nombre de points pour gagner</h3>
      <div class="slider-container">
        <input 
          type="range" 
          v-model="maxPoints" 
          min="10" 
          max="100" 
          step="5" 
          class="points-slider"
        />
        <span class="points-value">{{ maxPoints }} points</span>
      </div>
      <button @click="confirmPoints" class="btn-start">Confirmer</button>
    </div>


    
    <!-- Modified: Only show coin flip after points selection -->
    <div v-if="!gameStarted && pointsSelected && !result" class="coinContainer">
      <h3>tirage de l'attaquant</h3>
      <h3>Lancer la pièce !</h3>
      <div
        class="coin"
        :class="{ flipping: isFlipping }"
        @click="flipCoin"
      >
        <div class="face front"><img class="piece" src="@/assets/pile.png" /></div>
        <div class="face back"><img class="piece" src="@/assets/face.png" /></div>
      </div>
    </div>

    <!-- NO CHANGES: Result section -->
    <div v-if="result && !gameStarted" class="coinResult">
      <p>{{ result }} commence en tant qu'attaquant !</p>
      <button @click="startGame" class="btn-start">Commencer</button>
    </div>

    <!-- MODIFIED: Teams display section with activeTeam binding -->
    <div v-if="gameStarted" class="teams">
      <div
        class="team"
        :class="{ active: activeTeam === 'team1' }"
      >
        <h3>Équipe 1</h3>
        <p>Points : {{ scores.team1 }}</p>
        <ul>
          <li v-for="player in teams.team1" :key="player.name">{{ player.name }}</li>
        </ul>
      </div>
      <div
        class="team"
        :class="{ active: activeTeam === 'team2' }"
      >
        <h3>Équipe 2</h3>
        <p>Points : {{ scores.team2 }}</p>
        <ul>
          <li v-for="player in teams.team2" :key="player.name">{{ player.name }}</li>
        </ul>
      </div>
    </div>

    <!-- NO CHANGES: Game container -->
    <div v-if="gameStarted" class="gameContainer">
      <p class="gameInstructions">
        {{ gameInstruction }}
      </p>
      
      <!-- NO CHANGES: Balls section -->
      <div class="balls" v-if="currentRole === 'attacker'">
        <div class="whiteBall">
          <img
            :src="getImagePath(0)"
            alt="Balle blanche"
            class="ball"
            @click="handleWhiteBall"
          />
          <!-- MODIFIED: Added noBallTouched handler -->
           <div class="butContainer">
            <button @click="noBallTouched" class="btn-none">Aucune boule touchée</button>
            <button @click="noBallEntered" class="btn-none">Aucune boule rentrée</button>
          </div>
        </div>

        <div class="numberedBalls">
          <img
            v-for="n in 9"
            :key="n"
            :src="getImagePath(n)"
            :alt="`Boule ${n}`"
            class="ball"
            :class="{ 'selected': selectedBalls.includes(n) }"
            @click="toggleBallSelection(n)"
          />
        </div>

        <button 
          @click="endAttackerTurn" 
          class="btn-end-turn"
          :disabled="selectedBalls.length === 0"
        >
          Terminer le tour
        </button>
      </div>

      <!-- NO CHANGES: Defender section -->
      <div v-else-if="currentRole === 'defender'" class="defenderActions">
        <p>Les défenseurs replacent les boules rentrées par les attaquants.</p>
        <button @click="endDefenderTurn" class="btn-end-turn">Replacer terminé</button>
      </div>

      <!-- NO CHANGES: Messages section -->
      <div class="messages">
      <h3>Historique des actions</h3>
      <div class="messages-container">
        <div v-for="(msg, index) in messages" :key="index" class="message">
          <p>{{ msg }}</p>
        </div>
      </div>
    </div>
      <!-- MODIFIED: Updated popup for win condition -->
    <div v-if="showTurnPopup" class="popup-overlay">
      <div class="popup-content">
        <template v-if="winner">
          <h3>Félicitations !</h3>
          <p>L'équipe {{ winner === 'team1' ? '1' : '2' }} remporte la partie !</p>
          <div class="winners-list">
            <p>Les gagnants sont :</p>
            <ul>
              <li v-for="player in teams[winner]" :key="player.name">
                {{ player.name }}
              </li>
            </ul>
          </div>
          <button @click="goToHome" class="btn-start">OK</button>
        </template>
        <template v-else>
          <h3>Changement de tour</h3>
          <p>C'est au tour de l'équipe {{ currentTeam === 'team1' ? '1' : '2' }}</p>
          <button @click="closeTurnPopup" class="btn-start">OK</button>
        </template>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Impact9Game",
  data() {
    return {
      players: [],
      teams: { team1: [], team2: [] },
      isFlipping: false,
      result: "",
      gameStarted: false,
      scores: { team1: 0, team2: 0 },
      currentTeam: null,
      currentRole: "attacker",
      attemptsLeft: 3,
      messages: [],
      selectedBalls: [],
      showTurnPopup: false, // ADDED: New data property
      maxPoints: 20, // Default value
      pointsSelected: false,
      winner: null,
    };
  },
  computed: {
    gameInstruction() {
      if (this.currentRole === "attacker") {
        return `Équipe ${
          this.currentTeam === "team1" ? "1" : "2"
        } attaque : Vous avez ${this.attemptsLeft} coups restants pour rentrer une boule en touchant la boule 1 en premier.`;
      } else if (this.currentRole === "defender") {
        return `Équipe ${
          this.currentTeam === "team1" ? "2" : "1"
        } défend : Replacer les boules rentrées par l'équipe attaquante.`;
      }
      return "Erreur : rôle ou équipe non défini.";
    },
    // ADDED: New computed property for active team
    activeTeam() {
      if (this.currentRole === "attacker") {
        return this.currentTeam;
      } else {
        return this.currentTeam === "team1" ? "team2" : "team1";
      }
    }
  },
  // NO CHANGES: created hook
  created() {
    const savedPlayers = JSON.parse(localStorage.getItem("players")) || [];
    this.players = savedPlayers;
    this.createTeams();
  },
  methods: {
    confirmPoints() {
      this.pointsSelected = true;
    },

    goToHome() {
      this.$router.push('/');
    },
    // NO CHANGES: Existing methods
    getImagePath(n) {
      return require(`@/assets/ball_${n}.png`);
    },
    createTeams() {
      const shuffledPlayers = [...this.players].sort(() => Math.random() - 0.5);
      const mid = Math.ceil(shuffledPlayers.length / 2);
      this.teams.team1 = shuffledPlayers.slice(0, mid);
      this.teams.team2 = shuffledPlayers.slice(mid);
    },
    flipCoin() {
      if (this.isFlipping) return;

      this.isFlipping = true;
      this.result = "";
      setTimeout(() => {
        const random = Math.random() < 0.5 ? "Équipe 1" : "Équipe 2";
        this.result = random;
        this.currentTeam = random === "Équipe 1" ? "team1" : "team2";
        this.isFlipping = false;
        this.addMessage(`${random} commence en tant qu'attaquant.`);
      }, 2000);
    },
    startGame() {
      this.gameStarted = true;
      this.currentRole = "attacker";
    },
    toggleBallSelection(ballNumber) {
      const index = this.selectedBalls.indexOf(ballNumber);
      if (index === -1) {
        this.selectedBalls.push(ballNumber);
      } else {
        this.selectedBalls.splice(index, 1);
      }
    },
    noBallEntered() {
      this.attemptsLeft--;
      this.addMessage(`Aucune boule rentrée - ${this.attemptsLeft} coups restants`);
      
      if (this.attemptsLeft <= 0) {
        this.addMessage("Plus de coups disponibles - Changement d'équipe");
        this.currentTeam = this.currentTeam === "team1" ? "team2" : "team1";
        this.currentRole = "attacker";
        this.attemptsLeft = 3;
        this.selectedBalls = [];
        this.showTurnPopup = true; // Show popup
      }
    },
    // ADDED: New method for handling no ball touched
    noBallTouched() {
      this.addMessage("Aucune boule touchée - Changement d'équipe");
      this.currentTeam = this.currentTeam === "team1" ? "team2" : "team1";
      this.currentRole = "attacker";
      this.attemptsLeft = 3;
      this.selectedBalls = [];
      this.showTurnPopup = true; // Show popup
    },
    closeTurnPopup() {
      this.showTurnPopup = false;
    },
    // NO CHANGES: Remaining methods
    endAttackerTurn() {
      if (this.selectedBalls.length > 0) {
        const points = this.selectedBalls.reduce((sum, ball) => sum + ball, 0);
        this.scores[this.currentTeam] += points;

        this.selectedBalls.forEach((ball) => {
          this.addMessage(`L'équipe attaquante a rentré la boule ${ball}`);
        });

        // Check for winner
        if (this.scores[this.currentTeam] >= this.maxPoints) {
          this.winner = this.currentTeam;
          this.showTurnPopup = true;
          return;
        }

        this.addMessage(`Les défenseurs replacent les boules rentrées.`);
        this.selectedBalls = [];
        this.switchToDefenderPlacement();
        this.showTurnPopup = true;
      }
    },
    switchToDefenderPlacement() {
      this.currentRole = "defender";
    },
    endDefenderTurn() {
      this.addMessage("Les défenseurs ont replacé les boules.");
      this.restoreAttacker();
      this.showTurnPopup = true; // ADDED: Show popup when defender turn ends
    },
    restoreAttacker() {
      this.addMessage(
        `L'équipe attaquante reprend son tour.`
      );
      this.currentRole = "attacker";
      this.attemptsLeft = 3;
    },
    addMessage(msg) {
      this.messages.push(msg);
    },
  },
};
</script>

