}

.popup-content p {
  margin-bottom: 1.5rem;
  color: #f3f4f6;
  font-size: 1.2rem;
}
/* NO CHANGES: All styles remain the same */
.impact9Container {
  font-family: "Font1";
  height: 100%;
  width: 100%;
  background-color: #1d1d1d;
  color: #f3f4f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  align-content:center;
