import { createRouter, createWebHistory } from 'vue-router'
import HomeMenu from '../components/HomeMenu.vue'
import Games from '../views/GamesList.vue'
import Impact9Game from '../views/Impact9Game.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeMenu
  },
  {
    path: '/games',
    name: 'Games',
    component: Games
  },
  {
    path: '/Impact9Game',
    name: 'Impact9Game',
    component: Impact9Game
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router