<template>
  <div class="container">
    <div class="wrapper">
      <div class="content">
        <div class="button-group">
          <button @click="addPlayer" class="btn btn-add">+ Joueur</button>
          <button @click="removePlayer" class="btn btn-remove">- Joueur</button>
        </div>

        <div class="player-list">
          <div v-for="(player, index) in players" :key="index" class="player-item">
            <div class="player-number">{{ index + 1 }}</div>
            <input
              v-model="player.name"
              :placeholder="`Joueur ${index + 1}`"
              @input="savePlayers"
              class="player-input"
            />
          </div>
        </div>

        <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>

        <button 
          @click="goToGameList"
          :disabled="!allPlayersNamed"
          class="btn btn-navigate"
        >
          Liste des jeux
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeMenu',
  data() {
    return {
      players: [],
      errorMessage: ''
    }
  },
  computed: {
    allPlayersNamed() {
      return this.players.length > 0 && this.players.every(player => player.name.trim() !== '')
    }
  },
  created() {
    const savedPlayers = localStorage.getItem('players')
    if (savedPlayers) {
      this.players = JSON.parse(savedPlayers)
    }
  },
  methods: {
    addPlayer() {
      this.players.push({ name: '', score: 0 })
      this.savePlayers()
    },
    removePlayer() {
      if (this.players.length > 0) {
        this.players.pop()
        this.savePlayers()
      }
    },
    savePlayers() {
      localStorage.setItem('players', JSON.stringify(this.players))
      this.errorMessage = ''
    },
    goToGameList() {
      if (this.allPlayersNamed) {
        this.$router.push('/games')
      } else {
        this.errorMessage = 'Veuillez remplir tous les noms des joueurs'
      }
    }
  }
}
</script>

